import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import penderMiddleware from 'redux-pender';
import * as modules from './modules'

const reducers = combineReducers(modules);
const middlewares = [penderMiddleware()];

// Apply Redux Devtools only on Dev mode
const isDev = process.env.NODE_ENV === 'developement';
const devtools = isDev && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;
const composeEnhancers = devtools || compose;

// preloadedState is initial state after rendering serverside
const configure = (preloadedState) => createStore(reducers, preloadedState, composeEnhancers(applyMiddleware(...middlewares)));

export default configure;