import React, { Component } from 'react'
import styles from './MarkdownRender.scss'
import classNames from 'classnames/bind'

import marked from 'marked'

// Call the prism code
import Prism from 'prismjs'
import 'prismjs/themes/prism-okaidia.css'
// call the code structure
import 'prismjs/components/prism-bash.min.js'
import 'prismjs/components/prism-javascript.min.js'
import 'prismjs/components/prism-jsx.min.js'
import 'prismjs/components/prism-css.min.js'

const cx = classNames.bind(styles)

export default class MarkdownRender extends Component {
    state = {
        html: ''
    }

    renderMarkdown = () => {
        const { markdown } = this.props;
        // blank if there are no markdown
        if(!markdown) {
            this.setState({ html : ''})
            return
        }
        this.setState({
            html: marked(markdown, {
                breaks: true, // new line with enter
                sanitize: true // Rid on markdown html
            })
        })
    }

    constructor(props) {
        super(props)
        const { markdown } = props
        // To make work markdown on serverside
        this.state = {
            html: markdown ? marked(props.markdown, { breaks: true, sanitize: true }) : ''
        }
    }    

    componentDidUpdate(prevProps, prevState) {
        // Call renderMarkdown on change with markdown
        if(prevProps.markdown !== this.props.markdown) {
            this.renderMarkdown()
        }
        // Code highlighting on change of state
        if(prevState.html !== this.state.html) {
            Prism.highlightAll()
        }
    }

    componentDidMount() {
        Prism.highlightAll()
    }

    render() {
        const { html } = this.state

        // __html setting on the html to html rendering on the React
        const markup = {
            __html: html
        }

        // Need to put dangerouslySetinnerHTML on the object
        return (
            <div className={cx('markdown-render')} dangerouslySetInnerHTML={markup}/>
        )
    }
}