import React, { Component } from 'react'
import styles from './ModalWrapper.scss'
import classNames from 'classnames/bind'

const cx = classNames.bind(styles)

export default class ModalWrapper extends Component {
    state = {
        animate: false
    }

    startAnimations = () => {
        // Set animate value true
        this.setState({
            animate: true
        })
        // Set false after 250ms
        setTimeout(() => {
            this.setState({
                animate: false
            })
        }, 250)
    }

    componentDidUpdate(prevProps, prevState) {
        if(prevProps.visible !== this.props.visible) {
            this.startAnimations()
        }
    }

    render() {
        const { children, visible } = this.props
        const { animate } = this.state;

        // Only both of visible and animate is false
        // return null
        if(!visible && !animate) return null

        // Set animation on state
        const animation = animate && (visible ? 'enter' : 'leave')
        return (
            <div>
                <div className={cx('gray-background', animation)}/>
                <div className={cx('modal-wrapper')}>
                    <div className={cx('modal', animation)}>
                        {children}
                    </div>
                </div>
            </div>    
        )
    }
}