import React, { Component } from 'react'
import EditorHeader from '../../components/editor/EditorHeader/EditorHeader'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { withRouter } from 'react-router-dom'

import * as editorActions from '../../store/modules/editor'

import queryString from 'query-string'

class EditorHeaderContainer extends Component {
    componentDidMount() {
        const { EditorActions, location } = this.props;
        EditorActions.initialize() // initialize editor

        // query parsing
        const { id } = queryString.parse(location.search)
        if(id) {
            // Post when id exist
            EditorActions.getPost(id)
        }
    }

    handleGoback = () => {
        const { history } = this.props
        history.goBack()
    }

    handleSubmit = async () => {
        const { title, markdown, tags, EditorActions, history, location } = this.props
        const post = {
            title,
            body: markdown,
            // Eliminate duplications and divide tag text with ,
            tags: tags === "" ?[]:[...new Set(tags.split(',').map(tag => tag.trim()))]
        }
        try {
            // Call editPost if id exist
            const { id } = queryString.parse(location.search)
            if(id) {
                await EditorActions.editPost({id, ...post})
                history.push(`/post/${id}`)
                return
            }
            await EditorActions.writePost(post);
            // move the page. caution: postId don't make reference on top need to search this.props.postId in this site
            history.push(`/post/${this.props.postId}`)
        } catch(e) {
            console.log(e)
        }
    }

    render() {
        const { handleGoback, handleSubmit } = this
        const { id } = queryString.parse(this.props.location.search)
        return (
            <EditorHeader
                onGoBack={handleGoback}
                onSubmit={handleSubmit}
                isEdit={id ? true : false}
            />
        )
    }
}

export default connect(
    (state) => ({
        title: state.editor.get('title'),
        markdown: state.editor.get('markdown'),
        tags: state.editor.get('tags'),
        postId: state.editor.get('postId')
    }),
    (dispatch) => ({
        EditorActions: bindActionCreators(editorActions, dispatch)
    })
)(withRouter(EditorHeaderContainer))