import React, { Component } from 'react'
import PostList from '../../components/list/PostList/PostList'
import Pagination from '../../components/list/Pagination/Pagination'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as listActions from '../../store/modules/list'

class ListContainer extends Component {
    getPostList = () => {
        // Get the Page and tag value from the parent side
        const {tag, page, ListActions } = this.props
        ListActions.getPostList({
            page,
            tag
        })
    }

    componentDidMount() {
        this.getPostList()
    }

    componentDidUpdate(prevProps, prevState) {
        // Call list on change with Page/tag
        if(prevProps.page !== this.props.page || prevProps.tag !== this.props.tag) {
            this.getPostList()
            // Put the scrollbar on the top
            document.documentElement.scrollTop = 0
        }
    }

    render() {
        const { loading, posts, page, lastPage, tag } = this.props
        if(loading) return null // Nothing shows up during loading
        return (
            <div>
                <PostList posts={posts}/>
                <Pagination page={page} lastPage={lastPage} tag={tag}/>
            </div>
        )
    }
}

export default connect(
    (state) => ({
        lastPage: state.list.get('lastPage'),
        posts: state.list.get('posts'),
        loading: state.pender.pending['list/GET_POST_LIST']
    }),
    (dispatch) => ({
        ListActions: bindActionCreators(listActions, dispatch)
    })
)(ListContainer)
