import axios from 'axios'
import queryString from 'query-string'
const PORT = 'http://ec2-3-17-61-152.us-east-2.compute.amazonaws.com:4000';
export const writePost = ({title, body, tags}) => axios.post(`${PORT}/api/posts`, {title, body, tags})
export const getPost = (id) => axios.get(`${PORT}/api/posts/${id}`)
export const getPostList = ({tag, page}) => axios.get(`${PORT}/api/posts/?${queryString.stringify({tag, page})}`)
export const editPost = ({id, title, body, tags}) => axios.patch(`${PORT}/api/posts/${id}`, {title, body, tags})
export const removePost = (id) => axios.delete(`${PORT}/api/posts/${id}`)
export const login = (password) => axios.post(`${PORT}/api/auth/login`, { password })
export const checkLogin = () => axios.get(`${PORT}/api/auth/check`)
export const logout = () => axios.post(`${PORT}/api/auth/logout`)