import React, { Component } from 'react'
import styles from './EditorPane.scss'
import classNames from 'classnames/bind'

import CodeMirror from 'codemirror'

import 'codemirror/mode/markdown/markdown' // Color for the markdown 
// Code color in the markdown
import 'codemirror/mode/javascript/javascript';
import 'codemirror/mode/jsx/jsx'
import 'codemirror/mode/css/css'
import 'codemirror/mode/shell/shell'

// Css style for the CodeMirror
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/monokai.css'

const cx = classNames.bind(styles)

export default class EditorPane extends Component {

    editor = null // Editor ref
    codeMirror = null // CodeMirror instance
    cursor = null // Cursor location on the editor text

    initializeEditor = () => {
        this.codeMirror = CodeMirror(this.editor, {
            mode: 'markdown',
            theme: 'monokai',
            lineNumbers: true, // Put line number on left line
            lineWrapping: true // If it is too long write on next line
        })
        this.codeMirror.on('change', this.handleChangeMarkdown)
    }

    componentDidMount() {
        this.initializeEditor();
    }

    handleChange = (e) => {
        const { onChangeInput } = this.props
        const { value, name } = e.target
        onChangeInput({name, value})
    }

    handleChangeMarkdown = (doc) => {
        const { onChangeInput } = this.props
        this.cursor = doc.getCursor() // Save text cursor location
        onChangeInput({
            name: 'markdown',
            value: doc.getValue()
        })
    }

    componentDidUpdate(prevProps, prevState) {
        // Editor change on markdown change
        // Because text cursor location has initialised
        // Set location on the saved cursor 
        if(prevProps.markdown !== this.props.markdown) {
            const { codeMirror, cursor } = this
            if(!codeMirror) return // Before instance created
            codeMirror.setValue(this.props.markdown)
            if(!cursor) return
            codeMirror.setCursor(cursor)
        }
    }

    render() {
        const { handleChange } = this
        const { tags, title } = this.props
        
        return (
            <div className={cx('editor-pane')}>
                <input 
                    className={cx('title')} 
                    placeholder="Write the title" 
                    name="title"
                    value={title}
                    onChange={handleChange}
                />
                <div className={cx('code-editor')} ref={ref => this.editor=ref}></div>
                <div className={cx('tags')}>
                    <div className={cx('description')}>Tag</div>
                    <input 
                        name="tags" 
                        placeholder="Input the tag (seperate with ,)"
                        value={tags}
                        onChange={handleChange}
                    />
                </div>
            </div>
        )
    }
}